import { call } from './RageMPBrowserService';
import { BrazucasEventos } from "../../interfaces/brazucas";
export function autenticar(usuario, senha) {
    return call(BrazucasEventos.AUTENTICAR_JOGADOR, {
        usuario,
        senha,
    });
}
export function registrar(dados) {
    return call(BrazucasEventos.REGISTRAR_JOGADOR, dados);
}
export function recuperarAcesso(codigo, novaSenha) {
    return call(BrazucasEventos.RECUPERAR_ACESSO, {
        codigo,
        senha: novaSenha,
    });
}
